.TodoListitem {
padding: 1rem; // 18px
display: flex;
align-items: center;
&:nth-child(even) { background: #f8f9fa; }

.Checkbox { 
    cursor: pointer;
    flex: 1; 
    display: flex; 
    align-items: center;

    svg {
        font-size: 1.5rem;
    }

    .text {
        margin-left: 0.5rem;
        flex: 1;
    }


    &.checked { // 클래스 checked 가 붙으면
        svg { 
            color: #22b8cf; }

        .text {
            color: #adb5bd;
            text-decoration: line-through; }

    }

}

.remove { 
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ff6b6b;
    cursor: pointer;
}

& + & { border-top: 1px solid #dee2e6; } 
// & + & 인접 형제 선택자 - 모든 아이템에 border-top이 들어가야 함. 아이템에 가장 인접해 있는 아이템에 border-top을 지정해줘라
// 그럼 모든 아이템에 border-top이 지정됨. 
}